import { HttpErrorResponse } from '@angular/common/http';

export class ManageAccountNotFound {
  messages: string[];
  kind: 'ManageAccountNotFound' = 'ManageAccountNotFound';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class ManageAccountBadRequest {
  messages: string[];
  kind: 'ManageAccountBadRequest' = 'ManageAccountBadRequest';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class ManageAccountAccessDenied {
  messages: string[];
  kind: 'ManageAccountAccessDenied' = 'ManageAccountAccessDenied';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export class ManageAccountUnknownError {
  messages: string[];
  kind: 'ManageAccountUnknownError' = 'ManageAccountUnknownError';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = [response.message];
  }
}

export class ManageAccountConnectivityError {
  messages: string[];
  kind: 'ManageAccountConnectivityError' = 'ManageAccountConnectivityError';
  constructor(readonly response: HttpErrorResponse) {
    this.messages = ((response.error || {}).errors || []).map((e: any) => e.message);
  }
}

export type ManageAccountError =
  | ManageAccountAccessDenied
  | ManageAccountBadRequest
  | ManageAccountNotFound
  | ManageAccountConnectivityError
  | ManageAccountUnknownError;

export const parseError = (error: HttpErrorResponse): ManageAccountError => {
  if (error.error instanceof ErrorEvent) {
    return new ManageAccountUnknownError(error);
  } else {
    switch (error.status) {
      case 0:
        return new ManageAccountConnectivityError(error);
      case 401:
        return new ManageAccountAccessDenied(error);
      case 403:
        return new ManageAccountAccessDenied(error);
      case 400:
        return new ManageAccountBadRequest(error);
      case 404:
        return new ManageAccountNotFound(error);
      default:
        return new ManageAccountUnknownError(error);
    }
  }
};
