import { Injectable, TemplateRef } from '@angular/core';
import { HttpErrorResponse, HttpResponse } from '@angular/common/http';
import { BehaviorSubject, combineLatest, Observable, of, ReplaySubject } from 'rxjs';
import { catchError, distinctUntilChanged, map, scan, switchMap, take, tap } from 'rxjs/operators';
import {
  ArrangementsHttpService,
  AccountUserPreferences,
  ProductSummaryHttpService,
} from '@backbase/data-ang/arrangements';
import { NotificationService } from '@backbase/ui-ang/notification';
import { parseError, ManageAccountError } from './manage-account-error';
import { GetArrangementsRequestParams, UpdateVisibilityItem } from './model/types';
import {
  Accounts,
  ProductSummaryItem,
  GetProductsummaryRequest,
  HttpResponseType,
  PaginationType,
} from '@backbase/product-summary-common-ang';

const toDataServiceForVisibility = (state: ProductSummaryItem): UpdateVisibilityItem => {
  let visibility = state.userPreferences ? state.userPreferences.visible : state.visible;
  visibility = visibility !== undefined ? visibility : state.visible;
  return {
    arrangementId: state.id,
    visible: !visibility,
  };
};

const toDataServiceForAlias = (alias: string, arrangementId: string): AccountUserPreferences => ({
  arrangementId,
  alias,
});

/**
 * Service for fetching and storing the list of accounts
 *
 * This service relies on providers from `ProductSummaryManageAccountsWidgetModule`.
 *
 * @see ProductSummaryManageAccountsWidgetModule
 *
 * @usageNotes
 *
 * ### Ensure the ProductSummaryManageAccountsWidgetModule is imported to your component module
 *
 * ```ts
 * @NgModule({
 *   ...
 *   imports: [
 *     ...
 *     ProductSummaryManageAccountsWidgetModule,
 *   ],
 * })
 * export class MyWidgetModule {}
 * ```
 *
 * ### Inject this service into your component
 *
 * ```ts
 * @Component({
 *   ...
 *   providers: [ManageAccountsService],
 * })
 *  export class MyComponent {
 * ```
 */
@Injectable()
export class ManageAccountsService {
  /**
   * Constructor
   *
   * @param productSummaryDataService
   * @param manageAccountDataService
   * @param notification
   */
  constructor(
    private readonly productSummaryDataService: ProductSummaryHttpService,
    private readonly manageAccountDataService: ArrangementsHttpService,
    private readonly notification: NotificationService,
  ) {}

  /**
   * If the fetching of the list failed, this property
   * holds the errors coming from the server
   */
  readonly error = new BehaviorSubject<ManageAccountError | undefined>(undefined);
  /**
   * If errors occurred during patching arrangements record,
   * holds the errors coming from the server
   */
  readonly updateError = new BehaviorSubject<ManageAccountError | undefined>(undefined);
  /**
   * Whether the widget is currently fetching data from the server
   */
  readonly loading = new BehaviorSubject<boolean>(true);

  private readonly revokingComplete = new BehaviorSubject<boolean>(true);
  private readonly requestObject = new ReplaySubject<GetArrangementsRequestParams>(1);
  /**
   * Available product summary items.
   */
  readonly accountList: Observable<Accounts | undefined> = combineLatest(
    this.requestObject,
    this.revokingComplete,
  ).pipe(
    distinctUntilChanged(),
    // resetting error and loading values
    tap(() => this.error.next(undefined)),
    tap(() => this.loading.next(true)),
    switchMap(([requestObject]) => this.getAccountList(requestObject)),
    scan(
      (acc: Accounts, curr: Accounts): Accounts => ({
        count: curr.count,
        items: this.mergeResponses(acc, curr),
        params: curr.params,
      }),
    ),
    catchError((error: ManageAccountError) => {
      this.error.next(error);
      return of(undefined);
    }),
    tap(() => this.loading.next(false)),
  );

  private getAccountList(requestObject: GetProductsummaryRequest): Observable<Accounts> {
    const from = requestObject.from || 0;

    const paginationType = requestObject.paginationType || '';
    const requestObjWithoutPaginationType = { ...requestObject };
    delete requestObjWithoutPaginationType.paginationType;

    return this.productSummaryDataService
      .getArrangementsByBusinessFunction(requestObjWithoutPaginationType, HttpResponseType.RESPONSE)
      .pipe(
        map((res: HttpResponse<ProductSummaryItem[]>) => this.mapResponseWithCount(res, { from, paginationType })),
        catchError((error: HttpErrorResponse) => {
          throw parseError(error);
        }),
      );
  }

  private patchArrangementsRecord(
    accountUserPreferences: AccountUserPreferences,
    errorTemplateRef: TemplateRef<any> | string,
    successTemplateRef: TemplateRef<any> | string,
  ): Observable<HttpResponse<any> | undefined> {
    return this.manageAccountDataService.updateUserPreferences({ accountUserPreferences }).pipe(
      take(1),
      tap(() => this.revokingComplete.next(true)),
      tap(() => this.showNotification(successTemplateRef, 'success')),
      catchError((updateError: HttpErrorResponse) => {
        this.updateError.next(parseError(updateError));
        this.revokingComplete.next(true);
        this.showNotification(errorTemplateRef, 'error');
        return of(undefined);
      }),
    );
  }

  /**
   * Toggles visibility of product summary item.
   *
   * @param request
   * @param errorTemplateRef
   * @param successTemplateRef
   */
  toggleVisibility(
    request: ProductSummaryItem,
    errorTemplateRef: TemplateRef<any> | string,
    successTemplateRef: TemplateRef<any> | string,
  ) {
    this.patchArrangementsRecord(toDataServiceForVisibility(request), errorTemplateRef, successTemplateRef).subscribe();
  }

  /**
   * Updates alias of product summary item, by id.
   *
   * @param arrangementId
   * @param alias
   * @param errorTemplateRef
   * @param successTemplateRef
   */
  updateAlias(
    arrangementId: string,
    alias: string,
    errorTemplateRef: TemplateRef<any> | string,
    successTemplateRef: TemplateRef<any> | string,
  ) {
    const payloadForAlias = toDataServiceForAlias(alias, arrangementId);
    this.patchArrangementsRecord(payloadForAlias, errorTemplateRef, successTemplateRef).subscribe();
  }

  /**
   * Subscribes the passed `requestObject` to the local `requestObject`
   *
   * @param requestObject
   */
  getAccountsFrom(requestObject: Observable<GetArrangementsRequestParams>) {
    requestObject.subscribe(this.requestObject);
  }

  private showNotification(templateRef: TemplateRef<any> | string, modifier: 'success' | 'error') {
    this.notification.showNotification({
      header: templateRef,
      message: '',
      modifier,
    });
  }

  private mergeResponses(acc: Accounts, current: Accounts): ProductSummaryItem[] {
    if (
      acc &&
      acc.items &&
      current &&
      current.items &&
      acc.params.paginationType === PaginationType.LOAD_MORE &&
      current.params.from !== 0
    ) {
      return [...acc.items, ...current.items];
    }
    return current.items;
  }

  private mapResponseWithCount(
    response: HttpResponse<ProductSummaryItem[]>,
    { from, paginationType }: { from: number; paginationType: PaginationType | string },
  ): Accounts {
    // eslint-disable-next-line no-null/no-null
    if (response.body === null) {
      throw new Error();
    }
    const headerCount = response.headers ? response.headers.get('x-total-count') : undefined;
    const counter = headerCount ? parseInt(headerCount, 10) : (response.body && response.body.length) || 0;
    return {
      count: counter,
      items: response.body || [],
      params: { from, paginationType },
    };
  }
}
